.navbar {
    background-color: none;
}

.container-fluid{
    margin: 10px 25px;
    height: 100px;
    width: calc(100vw - 50px);
}

.navbar-toggler {
    display: flex;
    width: 36px;
    height: 33px;
    padding: 4px;
    border: 0px;
    
    img{
        width: 27px;
        height: 24px;
    }
}

.navbar-brand{
    font-size: 30px;
}

ul{
    display: flex;
    gap: 20px;
    justify-self: flex-end;
    list-style: none;
}
